import {useEffect } from 'react';
import Plotly from 'plotly.js/dist/plotly';

const Polarizer = ({ constants }) => {

  useEffect(() => {
    const pointCount = 720;
    const x1 = [];
    const y1 = [];

    for (let i = 0; i < pointCount; i++) {
      const rad = (Math.PI/180); // transformação para rad
      y1.push((Math.cos(constants.constantAlpha*rad)*Math.cos(i*rad/2))**2 + (Math.sin(constants.constantAlpha*rad)*Math.sin(i*rad/2))**2 + 2*Math.cos(constants.constantAlpha*rad)*Math.cos(i*rad/2)*Math.sin(constants.constantAlpha*rad)*Math.sin(i*rad/2)*Math.cos(constants.constantDelta*rad));
      x1.push(i/2);
    }

    const tracepolarizer = {
      type: 'scatter',
      mode: 'lines',
      name: 'Intensidade',
      opacity: 1,
      x: x1,
      y: y1,
      line: {
        width: 5,
        color: 'Orange',
      },
    };

    const layout = {
        title: 'Intensity as a function of the theta angle of the analyzer',
        xaxis: {
          title: 'Theta (degree)',
          range: [0, 360],
          grid: {
            width: 45,
          },
        },
        yaxis: {
          title: 'Intensity',
          range: [0, 1.1],
          grid: {
            width: 0.2,
          },
        },
      };

    Plotly.newPlot('myDiv3', [tracepolarizer], layout);
  }, [constants]);

  return (
    <div>
      <div id="myDiv3" style={{ width: '900px', height: '800px' }}></div>
    </div>
  );

  };
export default Polarizer;
