import {useEffect } from 'react';
import Plotly from 'plotly.js/dist/plotly';

const PolarizerPolar = ({ constants }) => {
    useEffect(() => {
      const pointCount = 720;
      const theta = [];
      const r = [];
  
      for (let i = 0; i < pointCount; i++) {
        const rad = (Math.PI / 180);
        const intensity = (Math.cos(constants.constantAlpha * rad) * Math.cos(i * rad / 2)) ** 2 + (Math.sin(constants.constantAlpha * rad) * Math.sin(i * rad / 2)) ** 2 + 2 * Math.cos(constants.constantAlpha * rad) * Math.cos(i * rad / 2) * Math.sin(constants.constantAlpha * rad) * Math.sin(i * rad / 2) * Math.cos(constants.constantDelta * rad);
        theta.push(i / 2);
        r.push(intensity);
      }
  
      const tracePolarizerPolar = {
        type: 'scatterpolar',
        mode: 'lines',
        name: 'Intensity',
        opacity: 1,
        r: r,
        theta: theta,
        line: {
          width: 5,
          color: 'Orange',
        },
      };
  
      const layout = {
        polar: {
          radialaxis: {
            visible: true,
            range: [0, 1.1],
          },
          angularaxis: {
            visible: true,
            range: [0, 360],
          },
        },
        showlegend: true,
        width: 900,
        height: 800,
        title: 'Intensity as a function of the theta angle of the analyzer',
      };
  
      Plotly.newPlot('myDiv4', [tracePolarizerPolar], layout);
    }, [constants]);
  
    return (
      <div>
        <div id="myDiv4" style={{ width: '900px', height: '800px' }}></div>
      </div>
    );
  };
  
  export default PolarizerPolar;