import { useEffect } from 'react';
import Plotly from 'plotly.js/dist/plotly';

const YZplane = ({ constants }) => {
  useEffect(() => {
    const pointCount = 750;
    const x1 = [];
    const y1 = [];

    for (let i = 0; i < pointCount; i++) {
      const angle = (i * Math.PI / 180);
      y1.push(Math.cos(angle) * Math.cos(constants.constantAlpha * Math.PI / 180));
      x1.push(Math.cos(angle - constants.constantDelta * Math.PI / 180) * Math.sin(constants.constantAlpha * Math.PI / 180));
    }

    const traceplane = {
      type: 'scatter',
      mode: 'lines',
      name: 'yz plane',
      x: x1,
      y: y1,
      opacity: 0.7,
      line: {
        width: 5,
        color: 'purple',
      },
    };

    const layout = {
      title: 'YZ-Plane',
      xaxis: {
        title: 'Y',
        range: [-1, 1],
        grid: {
          width: 0.2,
        },
      },
      yaxis: {
        title: 'Z',
        range: [-1, 1],
        grid: {
          width: 0.2,
        },
      },
    };

    Plotly.newPlot('myDiv2', [traceplane], layout);
  }, [constants]);

  return (
    <div>
      <div id="myDiv2" style={{ width: '900px', height: '800px' }}></div>
    </div>
  );
};

export default YZplane;
