import {useEffect } from 'react';
import Plotly from 'plotly.js/dist/plotly';

const Malus3d = ({ constants }) => {

  useEffect(() => {
    const pointCount = 750;
    const x1 = [];
    const y1 = [];
    const z1 = [];
    const c1 = [];

    for (let i = 0; i < pointCount; i++) {
      const angle = (i * Math.PI / 180); // Certifique-se de que 'constants' é um ângulo em radianos
      y1.push(Math.cos(angle)*Math.cos(constants.constantAlpha*Math.PI / 180));
      x1.push(Math.cos(angle - constants.constantDelta*Math.PI / 180)*Math.sin(constants.constantAlpha*Math.PI / 180));
      z1.push(i);
      c1.push(0);
    }

    const trace1 = {
      type: 'scatter3d',
      mode: 'lines',
      name: 'E_x component',
      x: z1,
      y: x1,
      z: c1,
      opacity: 0.7,
      line: {
        width: 10,
        color: 'blue',
      },
    };

    const trace2 = {
      type: 'scatter3d',
      mode: 'lines',
      name: 'E_y component',
      x: z1,
      y: c1,
      z: y1,
      opacity: 0.7,
      line: {
        width: 10,
        color: 'red',
      },
    };

    const trace3 = {
      type: 'scatter3d',
      mode: 'lines',
      name: 'Resulting component',
      x: z1,
      y: x1,
      z: y1,
      opacity: 0.7,
      line: {
        width: 15,
        color: 'purple',
      },
    };

    const layout = {
      title: '3D graph of the components of a polarized electric field',
      scene: {
        xaxis: {
          title: 'X',
          grid: {
            width: 0.2,
          },
        },
        yaxis: {
          title: 'Y',
          range: [-1, 1],
          grid: {
            width: 0.2,
          },
        },
        zaxis: {
          title: 'Z',
          range: [-1, 1],
          grid: {
            width: 0.2,
          },
        },
        
      },
    };

    Plotly.newPlot('myDiv', [trace1, trace2, trace3], layout);
  }, [constants]);

  return (
    <div>
      <div id="myDiv" style={{ width: '900px', height: '800px' }}></div>
    </div>
  );

  };
export default Malus3d;
