import { BlockMath } from "react-katex";
import Malus3d from "../../simulations/malus";
import ConstantMultiplier from '../../simulations/choose';
import { useState } from 'react';
import YZplane from "../../simulations/yzplane";
import Polarizer from "../../simulations/polarizer";
import PolarizerPolar from "../../simulations/polarizer-polar";
import styles from "./body.module.css"
function Body() {
  const malus = "I (\\theta) = \\langle (\\vec{E}(t) \\cdot \\hat{u})^2 \\rangle  ";
  const unit = "\\hat{u} = (\\cos(\\theta), \\sin(\\theta))"
  const field = "\\vec{E} = E_{0} (\\cos(\\alpha)\\cos(\\omega t), \\sin(\\alpha)\\cos(\\omega t - \\delta))"
  const [constants, setConstants] = useState({ constantAlpha: 1 });

  const handleConstantsUpdate = (constantAlpha, constantDelta, constantTheta) => {
      setConstants({ constantAlpha, constantDelta, constantTheta });
  };

  return (
      <div>
          <p><BlockMath math={malus}/> <BlockMath math={unit}/></p>
          <p><BlockMath math = {field} /></p>
          <div id = {styles.choose}>
            <ConstantMultiplier onUpdateConstants={handleConstantsUpdate} constants={constants}/>
          </div>
          <div id ={styles.graph}>
          <Malus3d constants={constants} />
          <YZplane constants={constants} />
          <Polarizer constants={constants} />
          <PolarizerPolar constants={constants}/>
          </div>

      </div>
  )
}

export default Body;
