import React, { useState } from 'react';
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import styles from './choose.module.css';

const ConstantMultiplier = ({ onUpdateConstants }) => {
    const [constantAlpha, setConstantAlpha] = useState(1);
    const [constantDelta, setConstantDelta] = useState(1);

    const handleConstantAlphaChange = (value) => {
      setConstantAlpha(value);
      onUpdateConstants(value, constantDelta);
    };

    const handleConstantDeltaChange = (value) => {
      setConstantDelta(value);
      onUpdateConstants(constantAlpha, value);
    };

    const sliderStyle = {
      width: '20%', // Altere a largura como desejar
      margin: 'left', // Centralize o slider horizontalmente
    };

    return (
      <div>
        <label class = {styles.labelc}>
          <span>Alpha:</span> <bold>{constantAlpha} degree </bold> (Angle between the first polarizer and the delta-wave plate)
          <Slider
          min={0}
          max={90}
          step={5}
          Value={constantAlpha}
          onChange={handleConstantAlphaChange}
          style={sliderStyle}
          class = {styles.sliderc}
          />
        </label>
        <br/>
        <label class = {styles.labelc}>
          <span>Delta:</span> <bold>{constantDelta} degree </bold> (Phase shift produced by the wave plate)
          <Slider
          min={0}
          max={180}
          step={5}
          value={constantDelta}
          onChange={handleConstantDeltaChange}
          style={sliderStyle}
          class = {styles.sliderc}
          />
        </label>
        <br/>
      </div>
    );
  };
  
  export default ConstantMultiplier;